* {
  margin: 0 auto;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}
body {
  /* ff 3.6+ */
  background: -moz-radial-gradient(
    ellipse at 53% 35%,
    rgba(76, 159, 175, 1) 34%,
    rgba(49, 220, 255, 1) 100%
  );

  /* safari 5.1+,chrome 10+ */
  background: -webkit-radial-gradient(
    ellipse at 53% 35%,
    rgba(76, 159, 175, 1) 34%,
    rgba(49, 220, 255, 1) 100%
  );

  /* opera 11.10+ */
  background: -o-radial-gradient(
    ellipse at 53% 35%,
    rgba(76, 159, 175, 1) 34%,
    rgba(49, 220, 255, 1) 100%
  );

  /* ie 10+ */
  background: -ms-radial-gradient(
    ellipse at 53% 35%,
    rgba(76, 159, 175, 1) 34%,
    rgba(49, 220, 255, 1) 100%
  );

  /* global 92%+ browsers support */
  background: radial-gradient(
    ellipse at 53% 35%,
    rgba(76, 159, 175, 1) 34%,
    rgba(49, 220, 255, 1) 100%
  );
}
.App {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  color: white;
}
.nav_wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90%;
  border-radius: 2rem;
  margin: 0px auto;
  padding: 0px 2rem;
  background-image: url("https://external-preview.redd.it/Tf2MC-orVRAI8OL5tjyF4k4H2Q6QLHTWllekGfqdt3c.png?width=960&crop=smart&auto=webp&s=be4ab9a2fa3ca2f7011799377f118a8dbc64aacb");
  background-size: cover;
  -webkit-box-shadow: 32px 14px 101px 5px rgba(8, 41, 107, 1);
  -moz-box-shadow: 32px 14px 101px 5px rgba(8, 41, 107, 1);
  box-shadow: 32px 14px 101px 5px rgba(8, 41, 107, 1),
    inset 6px 12px 6px rgba(255, 255, 255, 0.4);
}
.player {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
}
button {
  border-radius: 1rem;
  background: violet;
}
.playlist {
  display: flex;
  width: 30%;
  height: 90%;
  padding: 2rem 1rem;

  -webkit-box-shadow: 32px 14px 101px 5px rgba(8, 41, 107, 1);
  -moz-box-shadow: 32px 14px 101px 5px rgba(8, 41, 107, 1);
  box-shadow: 32px 14px 101px 5px rgba(8, 41, 107, 1),
    inset 6px 12px 6px rgba(255, 255, 255, 0.4);
}
.playlist_container {
  display: flex;
  justify-content: space-between;
  height: max-content;
  width: 100%;
  padding: 1rem 0.5rem;
  justify-content: space-around;
  align-items: center;
}
img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
}
button {
  background-color: #03072e;
  outnline: none;
  border: none;
  height: 40px;
  width: 120px;
  color: white;
}
